import { newsData } from "@/lib/data"
import { Container } from "@/components/container"
import Image from "next/image"
import { Calendar, User, ArrowLeft } from "lucide-react"
import { Badge } from "@/components/ui/badge"
import { Button } from "@/components/ui/button"
import Link from "next/link"
import { notFound } from "next/navigation"
import { Metadata } from "next"

// Force static generation for these paths
export async function generateStaticParams() {
    return newsData.map((news) => ({
        id: news.id.toString(),
    }))
}

export async function generateMetadata({ params }: { params: { id: string } }): Promise<Metadata> {
    const news = newsData.find((n) => n.id.toString() === params.id)

    if (!news) {
        return {
            title: "News Not Found",
        }
    }

    return {
        title: `${news.title} - Canary Academy`,
        description: news.excerpt,
        openGraph: {
            title: news.title,
            description: news.excerpt,
            images: news.image ? [news.image] : [],
            type: "article",
        },
    }
}

export default function NewsDetailPage({ params }: { params: { id: string } }) {
    const news = newsData.find((n) => n.id.toString() === params.id)

    if (!news) {
        notFound()
    }

    return (
        <div className="pt-20 min-h-screen bg-background">
            {/* Hero Section */}
            <section className="relative h-[50vh] min-h-[400px] w-full overflow-hidden">
                <Image
                    src={news.image || "/placeholder.svg"}
                    alt={news.title}
                    fill
                    className="object-cover"
                    priority
                />
                <div className="absolute inset-0 bg-black/60" />
                <Container className="relative z-10 h-full flex flex-col justify-end pb-12 text-white">
                    <div className="max-w-4xl">
                        <Badge className="bg-[#F5A623] hover:bg-[#FFB84D] text-white border-none mb-4">
                            {news.category}
                        </Badge>
                        <h1 className="text-3xl md:text-4xl lg:text-5xl font-bold mb-4 leading-tight">
                            {news.title}
                        </h1>
                        <div className="flex flex-wrap items-center gap-6 text-white/90">
                            <div className="flex items-center gap-2">
                                <Calendar className="w-5 h-5 text-[#F5A623]" />
                                <span>
                                    {new Date(news.date).toLocaleDateString("en-US", {
                                        year: "numeric",
                                        month: "long",
                                        day: "numeric",
                                    })}
                                </span>
                            </div>
                            <div className="flex items-center gap-2">
                                <User className="w-5 h-5 text-[#F5A623]" />
                                <span>Canary Academy</span>
                            </div>
                        </div>
                    </div>
                </Container>
            </section>

            {/* Content Section */}
            <section className="py-16">
                <Container>
                    <div className="grid lg:grid-cols-[1fr_300px] gap-12">
                        {/* Main Content */}
                        <article className="prose prose-lg max-w-none text-muted-foreground">
                            <p className="text-xl text-[#2C4F5E] font-medium leading-relaxed mb-8">
                                {news.excerpt}
                            </p>
                            <div className="space-y-6">
                                <p>
                                    Lorem ipsum dolor sit amet, consectetur adipiscing elit. Sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat.
                                </p>
                                <p>
                                    Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum.
                                </p>
                                <h3>Highlight of the Event</h3>
                                <p>
                                    Sed ut perspiciatis unde omnis iste natus error sit voluptatem accusantium doloremque laudantium, totam rem aperiam, eaque ipsa quae ab illo inventore veritatis et quasi architecto beatae vitae dicta sunt explicabo.
                                </p>
                                <div className="my-8 relative h-80 rounded-xl overflow-hidden shadow-premium">
                                    <Image
                                        src={news.image || "/placeholder.svg"}
                                        alt="Event details"
                                        fill
                                        className="object-cover"
                                    />
                                </div>
                                <p>
                                    Nemo enim ipsam voluptatem quia voluptas sit aspernatur aut odit aut fugit, sed quia consequuntur magni dolores eos qui ratione voluptatem sequi nesciunt.
                                </p>
                            </div>
                        </article>

                        {/* Sidebar */}
                        <aside className="space-y-8">
                            <div className="bg-white rounded-lg p-6 shadow-sm border border-border">
                                <h3 className="text-lg font-bold text-[#2C4F5E] mb-4">Back to News</h3>
                                <Button asChild variant="outline" className="w-full justify-start gap-2">
                                    <Link href="/news">
                                        <ArrowLeft className="w-4 h-4" />
                                        View All News
                                    </Link>
                                </Button>
                            </div>

                            {/* Related/Recent items could go here */}
                        </aside>
                    </div>
                </Container>
            </section>
        </div>
    )
}
